* {
  margin: 0;
  bottom: 0;
}

.App {
  color: #161616;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto;
  width: auto;
  background-color: #FCFCFC;
  /* background-color: green; */
  font-family: 'Poppins', sans-serif;
  /* background-color: #EEEDE6; */
  /* font-size: calc(10px + 1.3vmin); */
  /* font-size: calc(16px + 6 * ((100vw - 320px) / 680)); */
}

#name{
  color:#161616;
  font-size: 4vmax;
}

a{
  font-weight: 400;
  color:#161616;
}
a:visited {
  color:#161616;
}

a:selection{
  color:#161616;
}

a:hover{
color:#588BBC;
}


.content {
  height: auto;
  max-width: 40rem;
  margin-right: auto;
  margin-left: auto;

}

.NavbarContent {
  height: 20vh;
  width: 90%;
  display: flex;
align-items: flex-start;
justify-content: space-between;

}

.IntroHeader {
  width:auto;
  height: 100%;
}

.ThirdWidthIntroHeader{
  width:auto;
}

.info {
  width: 50%;
  display: flex;
}

.asbLogo {
  height: 10vh;
}

.logo {
  object-fit: cover;
  height: 100%;
  width: 100%;
}

/* HEADER HOME */
.aboutContent {
  display: block;
  margin-top: 1.5em;
  margin-bottom: 2em;
  font-size: 23px;
  font-size: 1.4375rem;
  margin-right: auto;
  margin-left: auto;
  width: 90%;
  /* background-color: red; */
}

.aboutContent p {
  font-size: 1.5rem;
  line-height: 1.3;
}

.projects {
  /* background-color: orange; */
  display: flex;
  flex-direction: column;
  height: auto;
  padding-top: 3rem;
  width:90%;
}
.list-section {
  max-width: 33rem;
  line-height: 1.25;
  font-size: 16px;
  font-size: 1rem;
  margin-right: auto;
  margin-left: auto;
}

.title {
  /* text-decoration: underline 0.15em  #2d9eef; */
  text-underline-offset: 0.2em;
  transition: text-decoration-color 300ms, text-underline-offset 300ms;
  line-height: 2;
  font-weight: 800;
}
.picture-container {

  background-repeat: no-repeat;
  background-position: relative;
  background-size: cover;
  border-radius: 50%; 
  display: block;
  /* background-image: url(./assets/aboutPicture.png); */
  height: 40vh;
  width: 40vh;
}

.picture-format {
  height: 100%;
  width: 100%;
  object-fit: contain;
}

.about-me-content {
  margin-top: 20%;
  background-color: olive;
}

.centered-flex{
  display: flex;
  align-items: center;
  flex-direction: column;
}


.main_container{
  background-color: E7E7E7;
  width: 100%;
  height: auto;
}

.content_container {
  background-color: blue;
  width: auto;
  height: 80vh;
  margin: 2%;
  border-radius: 8px;
  box-shadow: -1px 0px 27px -2px rgba(0,0,0,0.24);
  -webkit-box-shadow: -1px 0px 27px -2px rgba(0,0,0,0.24);
  -moz-box-shadow: -1px 0px 27px -2px rgba(0,0,0,0.24);
}

.content{
  height: 100%;
  width: 100%;
  margin: 2%;

}

.imgContainer {
width: auto;
height: 40vh;
border-radius: 8px;
/* background-color: green; */
margin-bottom: 6%;
}

.img_style{
  width: 100%;
  height: 100%;
  border-radius: 16px;
  object-fit: cover;
}
.slide{
  background-color: blue;
  width: 40vw;
  height: 40vh;
  object-fit: contain;
}


.Gallery_img{
  background-color: #161616;
  width: 40vw;
  height: 40vh;
  object-fit: contain;

}

.slide{
  background-color: blue;
  width: auto;
  height: auto;
  object-fit: contain;
}

.About_container{
  background-color: blue;
  width:100vw;
}